.question-container {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 0;
  box-shadow: 0 10px 40px rgba(31, 38, 135, 0.2);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  overflow: hidden;
  animation: fadeIn 0.6s ease-out;
}

/* Question header */
.question-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px 30px;
  color: white;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 15px;
}

.progress-fill {
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 3px;
  transition: width 0.5s ease;
}

.question-meta {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 10px;
}

.question-number {
  font-size: 1rem;
  font-weight: 600;
  opacity: 0.9;
}

.difficulty-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.topics {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.topic-tag {
  padding: 4px 10px;
  border-radius: 15px;
  font-size: 0.75rem;
  font-weight: 500;
  color: white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

/* Question content */
.question-content {
  padding: 30px;
}

.question-text {
  font-size: 1.4rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 20px;
  line-height: 1.5;
}

.question-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  padding: 10px 15px;
  background: rgba(103, 126, 234, 0.1);
  border-radius: 10px;
}

.time-indicator {
  font-size: 1rem;
  font-weight: 600;
  color: #667eea;
}

.estimated-time {
  font-size: 0.9rem;
  color: #666;
}

/* Answer options */
.answers-container {
  margin-bottom: 30px;
}

.answer-option {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  margin-bottom: 12px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: white;
  position: relative;
}

.answer-option:hover {
  border-color: #667eea;
  transform: translateX(5px);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.2);
}

.answer-option.selected {
  border-color: #667eea;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.answer-option.disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.answer-option.disabled:hover {
  transform: none;
  border-color: #e0e0e0;
  box-shadow: none;
}

.answer-option input[type="radio"] {
  width: 20px;
  height: 20px;
  margin-right: 15px;
  accent-color: #667eea;
  cursor: pointer;
}

.answer-text {
  flex: 1;
  font-size: 1rem;
  color: #333;
  line-height: 1.4;
  cursor: pointer;
}

/* Question actions */
.question-actions {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.submit-button {
  padding: 15px 40px;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(79, 172, 254, 0.4);
  position: relative;
  overflow: hidden;
}

.submit-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.submit-button:hover::before {
  left: 100%;
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.5);
}

.submit-button:active {
  transform: translateY(0);
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  background: #ccc;
  box-shadow: none;
}

.submit-button:disabled:hover {
  transform: none;
  box-shadow: none;
}

.submit-button:disabled::before {
  display: none;
}

/* Learning objective */
.learning-objective {
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.1) 0%, rgba(139, 195, 74, 0.1) 100%);
  border-left: 4px solid #4CAF50;
  border-radius: 0 10px 10px 0;
  padding: 20px;
}

.learning-objective h4 {
  color: #4CAF50;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.learning-objective p {
  color: #666;
  line-height: 1.5;
  margin: 0;
}

/* Responsive design */
@media (max-width: 768px) {
  .question-container {
    border-radius: 15px;
  }

  .question-header {
    padding: 15px 20px;
  }

  .question-content {
    padding: 20px;
  }

  .question-text {
    font-size: 1.2rem;
  }

  .question-meta {
    gap: 10px;
  }

  .difficulty-badge {
    font-size: 0.7rem;
    padding: 3px 8px;
  }

  .topic-tag {
    font-size: 0.7rem;
    padding: 3px 8px;
  }

  .answer-option {
    padding: 12px 15px;
  }

  .answer-option:hover {
    transform: translateX(3px);
  }

  .submit-button {
    padding: 12px 30px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .question-info {
    flex-direction: column;
    gap: 5px;
    text-align: center;
  }

  .question-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .topics {
    align-self: stretch;
  }

  .answer-option {
    padding: 10px 12px;
  }

  .answer-text {
    font-size: 0.95rem;
  }

  .learning-objective {
    padding: 15px;
  }
}

/* Animation for question transitions */
@keyframes questionSlideIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.question-container {
  animation: questionSlideIn 0.6s ease-out;
}

/* Focus styles for accessibility */
.answer-option:focus-within {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

.submit-button:focus {
  outline: 2px solid #4facfe;
  outline-offset: 3px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .answer-option {
    border-width: 3px;
  }
  
  .answer-option.selected {
    background: #f0f8ff;
    border-color: #0066cc;
  }
  
  .submit-button {
    background: #0066cc;
  }
}
