.feedback-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
  padding: 20px;
  box-sizing: border-box;
}

.feedback-container {
  background: white;
  border-radius: 20px;
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.4s ease-out;
  position: relative;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* Feedback header */
.feedback-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 25px 30px;
  border-radius: 20px 20px 0 0;
  color: white;
  display: flex;
  align-items: center;
  gap: 20px;
}

.feedback-header.correct {
  background: linear-gradient(135deg, #4CAF50 0%, #81C784 100%);
}

.feedback-header.incorrect {
  background: linear-gradient(135deg, #FF7043 0%, #FFAB91 100%);
}

.result-icon {
  font-size: 3rem;
  line-height: 1;
  animation: pulse 1.5s ease-in-out infinite;
}

.result-text {
  flex: 1;
}

.result-text h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0 0 8px 0;
}

.performance-message {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
  font-weight: 400;
}

.time-stats {
  text-align: right;
}

.time-spent {
  display: block;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 5px;
}

.time-estimate {
  font-size: 0.85rem;
  opacity: 0.8;
}

/* Feedback content */
.feedback-content {
  padding: 30px;
}

.feedback-content > div {
  margin-bottom: 30px;
}

.feedback-content > div:last-child {
  margin-bottom: 0;
}

.feedback-content h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Answer review */
.answer-display {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 15px 20px;
  border-left: 4px solid #ddd;
  margin-bottom: 15px;
}

.answer-display.correct-answer {
  background: rgba(76, 175, 80, 0.1);
  border-left-color: #4CAF50;
  color: #2E7D32;
}

.answer-display.wrong-answer {
  background: rgba(255, 112, 67, 0.1);
  border-left-color: #FF7043;
  color: #D84315;
}

.correct-answer-display {
  margin-top: 15px;
}

.correct-answer-text {
  background: rgba(76, 175, 80, 0.1);
  border-radius: 12px;
  padding: 15px 20px;
  border-left: 4px solid #4CAF50;
  color: #2E7D32;
  font-weight: 500;
}

/* Explanation */
.explanation-text {
  background: rgba(79, 172, 254, 0.1);
  border-radius: 12px;
  padding: 20px;
  border-left: 4px solid #4facfe;
  line-height: 1.6;
  color: #333;
}

/* Context section */
.insight-grid {
  display: grid;
  gap: 12px;
}

.context-item {
  background: #f8f9fa;
  padding: 12px 16px;
  border-radius: 8px;
  border-left: 3px solid #667eea;
}

.context-item strong {
  color: #667eea;
  margin-right: 8px;
}

.topic-insight {
  background: rgba(255, 193, 7, 0.1);
  border-left-color: #FFC107;
}

.topic-insight strong {
  color: #F57C00;
}

/* Next steps */
.advice-text {
  background: rgba(139, 195, 74, 0.1);
  padding: 15px 20px;
  border-radius: 12px;
  border-left: 4px solid #8BC34A;
  color: #333;
  line-height: 1.5;
  margin-bottom: 20px;
}

.assignment-preview {
  background: linear-gradient(135deg, rgba(255, 152, 0, 0.1) 0%, rgba(255, 193, 7, 0.1) 100%);
  border-radius: 12px;
  padding: 20px;
  border: 2px solid rgba(255, 152, 0, 0.3);
}

.assignment-preview h4 {
  color: #F57C00;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 10px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.assignment-hint {
  color: #666;
  margin: 0;
  font-style: italic;
}

/* Learning progress */
.progress-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.progress-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8f9fa;
  padding: 12px 16px;
  border-radius: 8px;
  border-left: 3px solid #667eea;
}

.progress-item span:first-child {
  color: #666;
  font-weight: 500;
}

.progress-item span:last-child {
  color: #333;
  font-weight: 600;
}

/* Feedback actions */
.feedback-actions {
  padding: 20px 30px 30px;
  text-align: center;
  border-top: 1px solid #f0f0f0;
}

.continue-button {
  padding: 15px 40px;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(79, 172, 254, 0.4);
  position: relative;
  overflow: hidden;
}

.continue-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.continue-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.5);
}

.continue-button:hover::before {
  left: 100%;
}

.continue-button:active {
  transform: translateY(0);
}

/* Animations */
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Responsive design */
@media (max-width: 768px) {
  .feedback-overlay {
    padding: 10px;
  }

  .feedback-container {
    max-height: 95vh;
  }

  .feedback-header {
    padding: 20px;
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .result-icon {
    font-size: 2.5rem;
  }

  .result-text h2 {
    font-size: 1.5rem;
  }

  .time-stats {
    text-align: center;
  }

  .feedback-content {
    padding: 20px;
  }

  .progress-summary {
    grid-template-columns: 1fr;
  }

  .insight-grid {
    gap: 10px;
  }

  .feedback-actions {
    padding: 15px 20px 25px;
  }

  .continue-button {
    padding: 12px 30px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .feedback-header {
    padding: 15px;
  }

  .result-text h2 {
    font-size: 1.3rem;
  }

  .performance-message {
    font-size: 0.9rem;
  }

  .feedback-content {
    padding: 15px;
  }

  .feedback-content h3 {
    font-size: 1.1rem;
  }

  .explanation-text,
  .advice-text,
  .assignment-preview {
    padding: 15px;
  }

  .context-item,
  .progress-item {
    padding: 10px 12px;
  }
}

/* Accessibility */
.feedback-container:focus {
  outline: none;
}

.continue-button:focus {
  outline: 2px solid #4facfe;
  outline-offset: 3px;
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .feedback-header.correct {
    background: #2E7D32;
  }
  
  .feedback-header.incorrect {
    background: #D84315;
  }
  
  .answer-display.correct-answer {
    background: #E8F5E8;
    border-left-color: #2E7D32;
  }
  
  .answer-display.wrong-answer {
    background: #FFEBEE;
    border-left-color: #D84315;
  }
  
  .continue-button {
    background: #0066cc;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .feedback-container {
    background: #1e1e1e;
    color: #f0f0f0;
  }
  
  .feedback-content h3 {
    color: #f0f0f0;
  }
  
  .answer-display {
    background: rgba(255, 255, 255, 0.1);
    border-left-color: #666;
  }
  
  .explanation-text {
    background: rgba(79, 172, 254, 0.2);
  }
  
  .context-item,
  .progress-item {
    background: rgba(255, 255, 255, 0.1);
  }
  
  .feedback-actions {
    border-top-color: #333;
  }
}

/* Print styles */
@media print {
  .feedback-overlay {
    position: static;
    background: none;
    backdrop-filter: none;
  }
  
  .feedback-container {
    box-shadow: none;
    max-height: none;
    overflow: visible;
  }
  
  .feedback-actions {
    display: none;
  }
}
