.assignment-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1100;
  animation: fadeIn 0.4s ease-out;
  padding: 20px;
  box-sizing: border-box;
}

.assignment-modal {
  background: white;
  border-radius: 20px;
  max-width: 900px;
  width: 100%;
  max-height: 95vh;
  overflow-y: auto;
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.4);
  animation: assignmentSlideIn 0.5s ease-out;
  position: relative;
}

@keyframes assignmentSlideIn {
  from {
    opacity: 0;
    transform: scale(0.8) translateY(30px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* Assignment header */
.assignment-header {
  background: linear-gradient(135deg, #FF6B6B 0%, #4ECDC4 100%);
  padding: 25px 30px;
  border-radius: 20px 20px 0 0;
  color: white;
  display: flex;
  align-items: center;
  gap: 20px;
}

.assignment-icon {
  font-size: 3rem;
  line-height: 1;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

.assignment-title {
  flex: 1;
}

.assignment-title h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0 0 8px 0;
}

.assignment-subtitle {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
  font-weight: 400;
}

.difficulty-badge {
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

/* Assignment intro */
.assignment-intro {
  padding: 30px;
}

.intro-content h3 {
  color: #333;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.encouragement {
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.1) 0%, rgba(139, 195, 74, 0.1) 100%);
  border-left: 4px solid #4CAF50;
  border-radius: 0 10px 10px 0;
  padding: 15px 20px;
  color: #2E7D32;
  font-weight: 500;
  margin-bottom: 25px;
}

.assignment-description,
.assignment-benefits,
.assignment-tips {
  margin-bottom: 25px;
}

.assignment-description h4,
.assignment-benefits h4,
.assignment-tips h4 {
  color: #667eea;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.assignment-description p {
  background: rgba(102, 126, 234, 0.1);
  padding: 15px 20px;
  border-radius: 10px;
  border-left: 4px solid #667eea;
  color: #333;
  line-height: 1.6;
  margin: 0;
}

.assignment-benefits ul,
.assignment-tips ul {
  margin: 0;
  padding-left: 20px;
}

.assignment-benefits li,
.assignment-tips li {
  margin-bottom: 8px;
  color: #555;
  line-height: 1.5;
}

.assignment-benefits li::marker {
  color: #4CAF50;
}

.assignment-tips li::marker {
  color: #FF9800;
}

/* Assignment actions */
.assignment-actions {
  text-align: center;
  padding-top: 20px;
}

.start-button {
  padding: 15px 40px;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #FF6B6B 0%, #4ECDC4 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
  position: relative;
  overflow: hidden;
}

.start-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.start-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.5);
}

.start-button:hover::before {
  left: 100%;
}

/* Assignment workspace */
.assignment-workspace {
  padding: 30px;
}

.workspace-header {
  margin-bottom: 25px;
}

.workspace-header h3 {
  color: #333;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.workspace-header p {
  color: #666;
  margin: 0;
  line-height: 1.5;
}

.task-reminder {
  background: rgba(255, 193, 7, 0.1);
  border-left: 4px solid #FFC107;
  border-radius: 0 10px 10px 0;
  padding: 15px 20px;
  margin-bottom: 25px;
}

.task-reminder h4 {
  color: #F57C00;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 8px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.task-reminder p {
  color: #333;
  margin: 0;
  line-height: 1.5;
}

/* Response section */
.response-section {
  margin-bottom: 25px;
}

.response-section label {
  display: block;
  margin-bottom: 10px;
}

.response-section h4 {
  color: #667eea;
  font-size: 1rem;
  font-weight: 600;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.response-textarea {
  width: 100%;
  min-height: 200px;
  padding: 15px 20px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  resize: vertical;
  transition: all 0.3s ease;
}

.response-textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.response-textarea::placeholder {
  color: #999;
  font-style: italic;
}

/* Workspace actions */
.workspace-actions {
  text-align: center;
}

.submit-response-button {
  padding: 12px 35px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #4CAF50 0%, #81C784 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

.submit-response-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(76, 175, 80, 0.5);
}

.submit-response-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  background: #ccc;
  box-shadow: none;
}

/* Assignment completion */
.assignment-completion {
  padding: 30px;
  text-align: center;
}

.completion-animation {
  font-size: 4rem;
  line-height: 1;
  margin-bottom: 20px;
  animation: celebrationSpin 1s ease-in-out;
}

@keyframes celebrationSpin {
  0% {
    transform: scale(0) rotate(0deg);
  }
  50% {
    transform: scale(1.2) rotate(180deg);
  }
  100% {
    transform: scale(1) rotate(360deg);
  }
}

.assignment-completion h3 {
  color: #4CAF50;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.completion-content p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 25px;
}

.what-you-practiced,
.your-response,
.next-steps-completion {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: left;
}

.what-you-practiced h4,
.your-response h4,
.next-steps-completion h4 {
  color: #667eea;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 10px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.what-you-practiced p,
.next-steps-completion p {
  color: #333;
  margin: 0;
  line-height: 1.5;
}

.response-display {
  background: rgba(76, 175, 80, 0.1);
  border-left: 4px solid #4CAF50;
  border-radius: 0 8px 8px 0;
  padding: 15px;
  font-family: 'Courier New', monospace;
  color: #2E7D32;
  white-space: pre-wrap;
  max-height: 200px;
  overflow-y: auto;
}

/* Completion actions */
.completion-actions {
  margin-top: 30px;
}

.continue-quiz-button {
  padding: 15px 40px;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(79, 172, 254, 0.4);
}

.continue-quiz-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.5);
}

/* Responsive design */
@media (max-width: 768px) {
  .assignment-overlay {
    padding: 10px;
  }

  .assignment-modal {
    max-height: 98vh;
  }

  .assignment-header {
    padding: 20px;
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .assignment-icon {
    font-size: 2.5rem;
  }

  .assignment-title h2 {
    font-size: 1.5rem;
  }

  .assignment-intro,
  .assignment-workspace,
  .assignment-completion {
    padding: 20px;
  }

  .response-textarea {
    min-height: 150px;
    padding: 12px 15px;
  }

  .start-button,
  .continue-quiz-button {
    padding: 12px 30px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .assignment-header {
    padding: 15px;
  }

  .assignment-title h2 {
    font-size: 1.3rem;
  }

  .assignment-subtitle {
    font-size: 0.9rem;
  }

  .assignment-intro,
  .assignment-workspace,
  .assignment-completion {
    padding: 15px;
  }

  .encouragement,
  .task-reminder,
  .what-you-practiced,
  .your-response,
  .next-steps-completion {
    padding: 12px 15px;
  }

  .completion-animation {
    font-size: 3rem;
  }

  .assignment-completion h3 {
    font-size: 1.3rem;
  }
}

/* Accessibility */
.assignment-modal:focus {
  outline: none;
}

.start-button:focus,
.submit-response-button:focus,
.continue-quiz-button:focus {
  outline: 2px solid currentColor;
  outline-offset: 3px;
}

.response-textarea:focus {
  outline: none;
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .assignment-header {
    background: #0066cc;
  }
  
  .start-button {
    background: #0066cc;
  }
  
  .submit-response-button {
    background: #2E7D32;
  }
  
  .continue-quiz-button {
    background: #0066cc;
  }
  
  .response-textarea {
    border-color: #333;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .assignment-modal {
    background: #1e1e1e;
    color: #f0f0f0;
  }
  
  .workspace-header h3,
  .assignment-completion h3 {
    color: #f0f0f0;
  }
  
  .workspace-header p,
  .completion-content p {
    color: #ccc;
  }
  
  .task-reminder,
  .what-you-practiced,
  .your-response,
  .next-steps-completion {
    background: rgba(255, 255, 255, 0.1);
  }
  
  .response-textarea {
    background: rgba(255, 255, 255, 0.05);
    border-color: #444;
    color: #f0f0f0;
  }
  
  .response-display {
    background: rgba(76, 175, 80, 0.2);
    color: #81C784;
  }
}
