.progress-tracker {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 25px;
  box-shadow: 0 10px 40px rgba(31, 38, 135, 0.2);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  animation: fadeIn 0.6s ease-out;
}

/* Header */
.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid #f0f0f0;
}

.progress-header h3 {
  color: #333;
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0;
}

.session-time {
  color: #666;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Stats grid */
.progress-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: linear-gradient(135deg, #f8f9ff 0%, #e8f2ff 100%);
  border-radius: 15px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  position: relative;
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.15);
  border-color: rgba(102, 126, 234, 0.2);
}

.stat-card.primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.stat-card.score {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.stat-card:hover::before {
  transform: scaleX(1);
}

.stat-icon {
  font-size: 2rem;
  line-height: 1;
  opacity: 0.9;
}

.stat-content {
  flex: 1;
}

.stat-label {
  font-size: 0.85rem;
  font-weight: 500;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 5px;
}

.stat-value {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1;
}

.score-value {
  font-size: 2rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

/* Topic analysis */
.progress-analysis {
  margin-bottom: 25px;
}

.topic-analysis {
  margin-bottom: 20px;
}

.topic-analysis h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.topic-analysis.strong h4 {
  color: #4CAF50;
}

.topic-analysis.weak h4 {
  color: #FF7043;
}

.topic-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.topic-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  transition: all 0.3s ease;
  cursor: default;
}

.topic-badge:hover {
  transform: scale(1.05);
}

.topic-badge.strong {
  background: linear-gradient(135deg, #4CAF50 0%, #81C784 100%);
  box-shadow: 0 2px 8px rgba(76, 175, 80, 0.3);
}

.topic-badge.weak {
  background: linear-gradient(135deg, #FF7043 0%, #FFAB91 100%);
  box-shadow: 0 2px 8px rgba(255, 112, 67, 0.3);
}

/* Progress bar section */
.progress-bar-section {
  margin-bottom: 25px;
}

.progress-label {
  font-size: 0.9rem;
  font-weight: 600;
  color: #666;
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.progress-bar {
  width: 100%;
  height: 12px;
  background: #e0e0e0;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  border-radius: 6px;
  transition: width 0.8s ease;
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.3) 50%, transparent 100%);
  animation: progressShine 2s ease-in-out infinite;
}

@keyframes progressShine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Adaptive insights */
.adaptive-insights {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  border-radius: 15px;
  padding: 20px;
  border-left: 4px solid #667eea;
}

.adaptive-insights h4 {
  color: #667eea;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.insights-grid {
  display: grid;
  gap: 12px;
}

.insight {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid rgba(102, 126, 234, 0.1);
}

.insight:last-child {
  border-bottom: none;
}

.insight-label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #666;
}

.insight-value {
  font-size: 0.9rem;
  font-weight: 600;
  color: #667eea;
}

/* Animations */
@keyframes statPulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}

.stat-card.primary {
  animation: statPulse 3s ease-in-out infinite;
}

/* Responsive design */
@media (max-width: 768px) {
  .progress-tracker {
    padding: 20px;
  }

  .progress-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .progress-stats {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .stat-card {
    padding: 15px;
  }

  .stat-icon {
    font-size: 1.5rem;
  }

  .stat-value {
    font-size: 1.5rem;
  }

  .score-value {
    font-size: 1.7rem;
  }

  .insights-grid {
    gap: 10px;
  }

  .insight {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
}

@media (max-width: 480px) {
  .progress-header h3 {
    font-size: 1.1rem;
  }

  .session-time {
    font-size: 0.8rem;
  }

  .stat-card {
    padding: 12px;
    gap: 10px;
  }

  .stat-icon {
    font-size: 1.3rem;
  }

  .stat-value {
    font-size: 1.3rem;
  }

  .score-value {
    font-size: 1.5rem;
  }

  .topic-badge {
    font-size: 0.75rem;
    padding: 4px 8px;
  }

  .adaptive-insights {
    padding: 15px;
  }
}

/* Focus styles for accessibility */
.stat-card:focus {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

.topic-badge:focus {
  outline: 2px solid currentColor;
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .stat-card {
    border: 2px solid #333;
  }
  
  .topic-badge.strong {
    background: #2E7D32;
    border: 1px solid #1B5E20;
  }
  
  .topic-badge.weak {
    background: #D84315;
    border: 1px solid #BF360C;
  }
  
  .progress-bar {
    border: 1px solid #333;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .progress-tracker {
    background: rgba(30, 30, 30, 0.95);
    color: #f0f0f0;
  }
  
  .progress-header {
    border-bottom-color: #444;
  }
  
  .stat-card {
    background: rgba(60, 60, 60, 0.8);
    color: #f0f0f0;
  }
  
  .adaptive-insights {
    background: rgba(102, 126, 234, 0.2);
  }
  
  .insight {
    border-bottom-color: rgba(102, 126, 234, 0.2);
  }
}
